import React, { useState, useEffect } from "react";
import "./../../assets/style.scss";
import { Navbar, Nav } from "react-bootstrap";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
// import { Link } from "react-scroll";
import $ from "jquery";
import { gsap, TweenMax, Power2 } from "gsap/all";
import { Link } from "react-router-dom";
import { useLocation } from "react-router";

const NavbarC = () => {
  let location = useLocation();
  useEffect(() => {
    $(".nav-link").mouseleave(function (e) {
      TweenMax.to($(this).children("span"), 0.3, {
        top: "50%",
        left: "50%",
        x: "0%",
        y: "0",
        ease: Power2.easeOut,
      });
    });

    $(".nav-link").mouseenter(function (e) {
      // TweenMax.to(this, 0.3, { height: 200, width: 200 });
    });

    $(".nav-link").mousemove(function (e) {
      callParallax(e, this, $(this).children("span"));
    });

    function callParallax(e, con, element) {
      parallaxIt(e, con, element, 40);
    }

    function parallaxIt(e, con, target, movement) {
      var $this = $(con);
      var relX = e.pageX - $this.offset().left;
      var relY = e.pageY - $this.offset().top;

      TweenMax.to(target, 0.3, {
        x: ((relX - $this.width() / 2) / $this.width()) * movement,
        y: ((relY - $this.height() / 2) / $this.height()) * movement,
        ease: Power2.easeOut,
      });
    }
  }, []);
  useEffect(() => {
    if(location.pathname=='/home'||location.pathname=='/projects'||location.pathname=='/partners'||location.pathname=='/contact'){
          var path = location.pathname.substring(1);
          var y = $("."+path+"").position().top;
          $("html, body").animate({ scrollTop: y }, 600);

    }
  }, [location]);

  return (
    <Navbar collapseOnSelect expand="lg" variant="dark">
      <Navbar.Brand href="#home">Ahmad algshy</Navbar.Brand>
      <Navbar.Toggle aria-controls="responsive-navbar-nav" />
      <Navbar.Collapse id="responsive-navbar-nav">
        <Nav className="ml-auto">
          <Link className="nav-link" to="/home">
            <span>Home</span>
          </Link>
          <Link className="nav-link" to="/projects">
            <span>Projects</span>
          </Link>
          <Link className="nav-link" to="/partners">
            <span>Partners</span>
          </Link>
          <Link to="/specificProject" className="nav-link">
            <span>How i do it</span>
          </Link>
          <Link className="nav-link" to="/contact">
            <span>Contact</span>
          </Link>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  );
};

export default NavbarC;
