import React from "react";
import { useParams } from "react-router-dom";
const Iframe = (props) => {
  let { id } = useParams();
  return (
    <div className="iframeCon">
      {props.videos[id].map((video) => (
        <div className="iframe">
          <iframe width="420" height="315" src={video}></iframe>
        </div>
      ))}
    </div>
  );
};

export default Iframe;
