import React, { useEffect, useState } from "react";
import "./../assets/style.scss";
import { Container, Row, Col, Button } from "react-bootstrap";
import businessmanThumbUp from "./../assets/images/businessmanThumbUp.png";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

import CircleTitle from "./../components/circleTitle/circleTitle";
import ProjectCard from "./../components/projectCard/projectCard";

import { useHistory } from "react-router-dom";
import $ from "jquery";

const Projects = () => {
  if (typeof window !== "undefined") {
    gsap.registerPlugin(ScrollTrigger);
  }
  useEffect(() => {
    // let sections = gsap.utils.toArray(".home-pic-con .brown-circle")
    // console.log(sections);
    gsap.to(".projects-pic", {
      // scale: 5,
      duration: 3,
      scrollTrigger: {
        trigger: ".projects-pic",
        pin: true,
        scrub: 3,
        start: "40% 50%",
        end: "210% 50%",
        // scale:3,
        // snap:5000,
        // end: () => "+=" + document.querySelector(".home-pic-con .brown-circle").offsetWidth
      },
    });
    gsap.to(".ProjectsButtons", {
      y: 0,
      duration: 1,
      opacity: 1,
      scrollTrigger: {
        trigger: ".ProjectsButtons",
        // pin: true,
        scrub: 3,
        start: "top bottom",
        end: "top 80%",
        // scale:3,
        snap: 5000,
        // end: () => "+=" + document.querySelector(".home-pic-con .brown-circle").offsetWidth
      },
    });
    gsap.to(".circleTitle", {
      y: 0,
      duration: 1,
      opacity: 1,
      scrollTrigger: {
        trigger: ".circleTitle",
        // pin: true,
        scrub: 3,
        start: "top bottom",
        end: "top 80%",
        // scale:3,
        snap: 5000,
        // end: () => "+=" + document.querySelector(".home-pic-con .brown-circle").offsetWidth
      },
    });
    gsap.to(".projectCard", {
      y: 0,
      duration: 1,
      opacity: 1,
      scrollTrigger: {
        trigger: ".projectCard",
        // pin: true,
        scrub: 3,
        start: "top bottom",
        end: "top 80%",
        // scale:3,
        snap: 5000,
        // end: () => "+=" + document.querySelector(".home-pic-con .brown-circle").offsetWidth
      },
    });
  });
  useEffect(() => {
    $(".ProjectsButtons button").click(function () {
      $(".ProjectsButtons button").removeClass("selected");
      $(this).addClass("selected");
    });
  }, []);
  const history = useHistory();
  const [filter, setFilter] = useState({
    ui: true,
    video: true,
    design: true,
  });
  return (
    <div className="projects">
      <Container>
        <Row>
          <Col lg={4}>
            <img className="projects-pic" src={businessmanThumbUp} />
          </Col>
          <Col lg={{ span: 7, offset: 1 }}>
            <CircleTitle size="lg">
              My job is to help you out to reach the stars.{" "}
            </CircleTitle>
            <CircleTitle size="md">Latest projects</CircleTitle>
            <div className="ProjectsButtons">
              <Button
                className="selected"
                onClick={() =>
                  setFilter({ ui: true, video: true, design: true })
                }
              >
                All
              </Button>
              <Button
                onClick={() =>
                  setFilter({ ui: true, video: false, design: false })
                }
              >
                UI/UX
              </Button>
              <Button
                onClick={() =>
                  setFilter({ ui: false, video: true, design: false })
                }
              >
                Video editing
              </Button>
              <Button
                onClick={() =>
                  setFilter({ ui: false, video: false, design: true })
                }
              >
                Graphic design
              </Button>
            </div>
            <ProjectCard
              filter={filter.ui}
              date="March 2021"
              title="DieiTech"
              links={[
                { title: "Website", link: "/DieiTech" },
                { title: "Dark mode", link: "/DieiTechDark" },
              ]}
            >
              DieiTech Established in 2002 and rebranded in 2019 to provide
              reliable, honest to and expert solutions and services, Specialized
              Developers, Engineers, Designers are here for you to help you
              achieve your goals.
            </ProjectCard>
            <ProjectCard
              type="design"
              filter={filter.design}
              date="July 2021"
              title="Yara"
              links={[{ title: "View", link: "/Yara" }]}
            >
              Yara Beauty Makeup Salon is a special amazing Salon based in UAE,
              Gives general makeup services. Their primary duties include
              enhancing facial aesthetics through makeup, creating custom looks
              for clients
            </ProjectCard>
            <ProjectCard
              filter={filter.video}
              date="Feb 2021"
              title="Dova"
              links={[{ title: "View", link: "/videoEditing/0" }]}
            >
              Dova is the first online store in Switzerland in three languages:
              Arabic, English, German, that offers modest, high-quality,
              practical .attires We are endeavored every day to choose the
              utmost comfortable materials and classy styles that can be ordered
              around the clock, ready to be in your wardrobe in few simple
              steps.
            </ProjectCard>

            <ProjectCard
              filter={filter.ui}
              date="March 2021"
              title="Ticketate"
              links={[
                { title: "Website", link: "/Ticketate" },
                { title: "Mobile", link: "/TicketateMobile" },
              ]}
            >
              Ticketate is an online website for selling sports game tickets
              stars.{" "}
            </ProjectCard>
            <ProjectCard
              filter={filter.ui}
              date="May 2021"
              title="Digoria"
              links={[{ title: "Website", link: "/Digoria" }]}
            >
              Digoria company is one of the UK's leading independent building
              services companies. It provides a complete range of electrical,
              mechanical, security, and commercial catering equipment services.
              stars.{" "}
            </ProjectCard>
            <ProjectCard
              filter={filter.ui}
              date="june 2021"
              title="Red Stone"
              links={[{ title: "Website", link: "/RedStone" }]}
            >
              Red Stone is a purely industrial company dedicated to
              manufacturing industrial silicone using the finest and best raw
              materials, and obtaining satisfaction that exceed customers'
              expectations.
            </ProjectCard>
            <ProjectCard
              filter={filter.ui}
              date="june 2021"
              title="Swirkies"
              links={[{ title: "Website", link: "/Swirkies" }]}
            >
              Swirkies is An ancient experience and distinct, We are proud of
              Serving luxury happiness through our international delicacies
              desserts Our goal is to provide unique distinctive taste and rich
              flavor Luxury service.
            </ProjectCard>
            <ProjectCard
              filter={filter.video}
              date="Oct 2018"
              title="Routine"
              links={[{ title: "View", link: "/videoEditing/1" }]}
            >
              Routine is a short film that talks about depression and fear
              affecting a teenager's life and how it all passes their time
            </ProjectCard>
            <ProjectCard
              filter={filter.ui}
              links={[{ title: "contact", link: "/contact" }]}
              date="Since you contact me"
              title="Do you want Your dreams to come true?"
            >
              Your project could be one of those project in the feature, And I
              will be glad to have you to work together and be by your side
              until your project comes to the real world.
            </ProjectCard>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default Projects;
