import React, { useEffect, useState } from "react";
import "./../assets/style.scss";
import { Container, Row, Col, Button } from "react-bootstrap";
import { Facebook, Linkedin } from "react-bootstrap-icons";
import AOS from "aos";
import "aos/dist/aos.css";
import { send } from "emailjs-com";
import businessman from "./../assets/images/business-3d.png";

const Contact = () => {
  const [toSend, setToSend] = useState({
    full_name: "",
    email: "",
    subject: "",
    message: "",
  });
  useEffect(() => {
    AOS.init({
      duration: 1000,
    });
  }, []);
  const sendEmail = (e) => {
    e.preventDefault();
    send(
      "service_vp314f8",
      "template_0jdg63g",
      toSend,
      "user_MRVNQeE1FygQ63XLJIkvg"
    )
      .then((response) => {
        // console.log("SUCCESS!", response.status, response.text);
      })
      .catch((err) => {
        // console.log("FAILED...", err);
      });
  };
  const handleChange = (e) => {
    setToSend({ ...toSend, [e.target.name]: e.target.value });
  };
  return (
    <div className="contact">
      <Container>
        <Row>
          <Col md="6">
            <div className="title">
              <span>Contact me</span>
              <div className="brownCircle"></div>
            </div>
            <div className="paragraph">
              Whenever, wherever. We're meant to work together.
            </div>
            <form onSubmit={sendEmail}>
              <input
                type="text"
                value={toSend.full_name}
                onChange={handleChange}
                placeholder="Full name"
                name="full_name"
              />
              <input
                type="email"
                value={toSend.email}
                onChange={handleChange}
                placeholder="Email"
                name="email"
              />
              <input
                type="text"
                value={toSend.subject}
                onChange={handleChange}
                placeholder="Subject"
                name="subject"
              />
              <textarea
                rows="4"
                value={toSend.message}
                onChange={handleChange}
                type="text"
                placeholder="Message"
                name="message"
              />
              <Button type="submit">Submit</Button>
            </form>
          </Col>
          <Col
            md="6"
            className="contact-pic-con"
            data-aos="zoom-in-up"
            data-aos-delay="300"
          >
            <div className="brown-circle"></div>
            <img className="contact-pic" src={businessman} />
          </Col>
        </Row>
        <div className="media">
          <a
            href="https://www.facebook.com/ahmdalgshy"
            className="circle"
          >
            <Facebook />
          </a>
          <a
            href="https://sy.linkedin.com/in/ahmad-algshy-06a78a1a7"
            className="circle"
          >
            <Linkedin />
          </a>
        </div>
      </Container>
    </div>
  );
};

export default Contact;
