import React,{useState} from 'react';

import PartnerIcon from './partnerIcon'


const Slider = () => {
    const [active,setActive] = useState(0);
    const [partners,setPartners] = useState([
        {
            img:'/images/ahmad.jpeg',
            name:'Ahmad Al-Sabbagh',
            job:'Directgroup CO-Founder MySlide Sales Manager',
            paragraph:'I have no problem working with anyone but working with professional and competent people like Ahmed Al-Jgshy makes me feel that I can achieve my goals better and faster'
        },
        {
            img:'/images/leen.jpeg',
            name:'Leen Alfalah',
            job:'3D web developer',
            paragraph:"Ahmed is one of the most creative people I have worked with him. He has a wide imagination and inspiration for great ideas quickly. He's smart. I hope to work with him again and again so that we can do other nice projects in the future."
        },
        {
            img:'/images/dova.jpeg',
            name:'Dova Store',
            job:'',
            paragraph:'As a Dova Team, Ahmad is a Video editor, Website editor, and An essential part of the team. He is able to perform all the tasks entrusted to him professionally and in record time, very nice to deal and has high skills to work with the team. Besides, he accepts different opinions and aimed to develop his skills and knowledge always'
        },
        {
            img:'/images/farid.jpeg',
            name:'Mohammad farid farid',
            job:'Social media coordinator',
            paragraph:'My opinion remains insufficient when describing a person out of the ordinary! Ahmed Al-Jishi embodies a great source of inspiration for me, a necessary help station, not only in his career but in several professional fields, as he is, as I referred previously, out of the ordinary and multi-talented'
        },
    ]);
    const next = () =>{
        if(active!=partners.length-1)
            setActive(active+1);
    }
    const prev = () =>{
        if(active!=0)
            setActive(active-1);
    }
    return (
        <div className="slider">
            <div className="arrows">
                <button className="arrowBtn" onClick={prev}>
                <svg xmlns="http://www.w3.org/2000/svg" width="22" height="22" fill="currentColor" className="bi bi-arrow-left" viewBox="0 0 16 16">
                    <path fillRule="evenodd" d="M15 8a.5.5 0 0 0-.5-.5H2.707l3.147-3.146a.5.5 0 1 0-.708-.708l-4 4a.5.5 0 0 0 0 .708l4 4a.5.5 0 0 0 .708-.708L2.707 8.5H14.5A.5.5 0 0 0 15 8z"/>
                </svg>
                </button>
                <div className="sliderPages">
                    {active+1+"/"+partners.length}
                </div>
                <button className="arrowBtn" onClick={next}>
                <svg xmlns="http://www.w3.org/2000/svg" width="22" height="22" fill="currentColor" className="bi bi-arrow-right" viewBox="0 0 16 16">
                    <path fillRule="evenodd" d="M1 8a.5.5 0 0 1 .5-.5h11.793l-3.147-3.146a.5.5 0 0 1 .708-.708l4 4a.5.5 0 0 1 0 .708l-4 4a.5.5 0 0 1-.708-.708L13.293 8.5H1.5A.5.5 0 0 1 1 8z"/>
                </svg>
                </button>
            </div>
            <PartnerIcon icon={partners[active].img} name={partners[active].name} job={partners[active].job}/>
            <div className="paragraph">
                {partners[active].paragraph}
            </div>
        </div>
    );
};

export default Slider;