import React, { useState, useEffect } from "react";

import "./../assets/style.scss";
import { Navbar, Nav, NavDropdown } from "react-bootstrap";
import { Container, Row, Col } from "react-bootstrap";

import NavbarC from "./../components/navbar/navbar";
import Home from "./../pages/home";
import Projects from "./../pages/projects";
import SpecificProject from "./../pages/specificProject";
import Partners from "./../pages/partners";
import Contact from "./../pages/contact";
import $ from "jquery";
import AllPages from "../components/allPages/allPages";
import Iframe from "../components/Iframe/Iframe";
import { useLocation } from "react-router";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import { gsap, TweenMax, Power2 } from "gsap/all";

// pdf



function App() {
  useEffect(() => {
    window.scroll(0, 0);
    const CURSOR = $(".cursor");
    const BLOBBER = $(".nav-link");
    $(document).mousemove(function (e) {
      TweenMax.to(CURSOR, 0.3, {
        top: e.pageY - CURSOR.height() / 2,
        left: e.pageX - CURSOR.width() / 2,
        ease: Power2.easeOut,
      });
    });
    const initCursor = () => CURSOR.removeClass("big");
    const setCursorHover = () => CURSOR.addClass("big");
    const removeCursorHover = () => CURSOR.removeClass("big");
    setTimeout(function () {
      initCursor();
    }, 1000);
    BLOBBER.each(function () {
      $(this).on({
        mouseover: setCursorHover,
        mouseleave: removeCursorHover,
      });
    });
    $(window).scroll(function () {
      if ($(window).scrollTop() > 200) $(".navbar").addClass("bg");
      else $(".navbar").removeClass("bg");
    });
  }, []);
  const [videos, setVideos] = useState([
    [
      "https://drive.google.com/file/d/1qo0AwMQiCZnpEn2pPFqd2H0tBU25BywD/preview",
      "https://drive.google.com/file/d/18mLzbvTbMBas7ZSrwhTsSIfYrNUnqfbh/preview",
      "https://drive.google.com/file/d/1-eUGt-sNN3kIZQfXlgVrhBA7s2r3Osmy/preview",
      "https://drive.google.com/file/d/1E4RgGYYYnJWjAQzZ4lv7ZDmAJMzxVyMP/preview",
      "https://drive.google.com/file/d/1D7UDCCBLrEJiZO4vDZBjG7Z4XAKAHFLS/preview",
      "https://drive.google.com/file/d/1Ewx_rtJ1VTAj-UJE2GyRrAj-gS60eTB4/preview",
      "https://drive.google.com/file/d/1a72TFcn-UlfFmeafMCeg4ZwC30WedMHE/preview",
      "https://drive.google.com/file/d/1ih0U6-StsdQPTQOw3HghbaVIo0z4C0cR/preview",
    ],
    [
      "https://www.youtube.com/embed/Tdl7I-8i0G8"
    ],
  ]);

  return (
    <Router>
      <div className="App">
        <div className="cursor"></div>
        {/* <div className="cursor cursor2"></div> */}
        <NavbarC />
        <Switch>
          <Route
            exact
            path={["/home", "/", "/projects", "/partners", "/contact"]}
          >
            <Home />
            <Projects />
            <Partners />
            <Contact />
          </Route>
          <Route path="/specificProject">
            <SpecificProject />
          </Route>
          <Route path="/videoEditing/:id">
            <Iframe videos={videos}/>
          </Route>
          <Route path="/DieiTech">
            <AllPages pdf="/pdf/AldtTechWebsite.pdf" />
          </Route>
          <Route path="/DieiTechDark">
            <AllPages pdf="/pdf/AldtTechWebsiteDarkmode.pdf" />
          </Route>
          <Route path="/Ticketate">
            <AllPages pdf="/pdf/TicketateWebView.pdf" />
          </Route>
          <Route path="/TicketateMobile">
            <AllPages pdf="/pdf/TicketateMobileView.pdf" />
          </Route>
          <Route path="/Digoria">
            <AllPages pdf="/pdf/Digoria2.pdf" />
          </Route>
          <Route path="/RedStone">
            <AllPages pdf="/pdf/RedStoneComingSoon.pdf" />
          </Route>
          <Route path="/Swirkies">
            <AllPages pdf="/pdf/SwirkiesWebsite.pdf" />
          </Route>
          <Route path="/Yara">
            <AllPages pdf="/pdf/yaraLogo.pdf" />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
